<template>
  <el-dialog title="" class="my-dialog"
            :visible.sync="dialogVisible"
            :close-on-click-modal="false" width="100%" center
            @close='resetDialog'>
    <el-form ref="cureForm" :model="cureForm" label-width="auto" label-position="left">

      <el-form-item label="">
        <el-switch v-model="cureForm.is_jiehe" active-text="" inactive-text=""></el-switch>
      </el-form-item>

      <div>
        <div>
          <h3></h3>
          <h4>(1)</h4>
          <el-form-item label="">
            <el-checkbox v-for="(val, key) in mydata.xu"
            :key="key" :label="val" v-model="cureForm[key]">
            </el-checkbox>
            <el-input v-model="cureForm.xu_qita"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-checkbox v-for="(val, key) in mydata.shi"
            :key="key" :label="val" v-model="cureForm[key]">
            </el-checkbox>
            <el-input v-model="cureForm.shi_qita"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-checkbox v-for="(val, key) in mydata.xushi"
            :key="key" :label="val" v-model="cureForm[key]">
            </el-checkbox>
            <el-input v-model="cureForm.xushi_qita"></el-input>
          </el-form-item>
          <h4>(2)</h4>
          <el-form-item label="">
            <el-input v-model="cureForm.xuanji"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="cureForm.zucheng"></el-input>
          </el-form-item>
          <h4>(3)</h4>
          <el-form-item label="">
            <el-switch v-model="cureForm.zhongcheng" active-text="" inactive-text=""></el-switch>
            <el-input v-model="cureForm.zhongcheng_juti" v-show="cureForm.zhongcheng" placeholder=""></el-input>
          </el-form-item>
        </div>
        <div>
          <h3></h3>
          <el-form-item label="">
            <el-switch v-model="cureForm.gaoxiong" active-text="" inactive-text=""></el-switch>
            <el-input v-model="cureForm.gaoxiong_juti" v-show="cureForm.gaoxiong" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-switch v-model="cureForm.yidao" active-text="" inactive-text=""></el-switch>
            <el-input v-model="cureForm.yidao_juti" v-show="cureForm.yidao" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-switch v-model="cureForm.tiaozhou" active-text="" inactive-text=""></el-switch>
            <el-input v-model="cureForm.tiaozhou_juti" v-show="cureForm.tiaozhou" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-switch v-model="cureForm.pailuan" active-text="" inactive-text=""></el-switch>
            <el-input v-model="cureForm.pailuan_juti" v-show="cureForm.pailuan" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-switch v-model="cureForm.shoushu" active-text="" inactive-text=""></el-switch>
            <el-input v-model="cureForm.shoushu_juti" v-show="cureForm.shoushu" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="cureForm.gaoxiong_juti"></el-input>
          </el-form-item>
        </div>

      </div>
    </el-form>

    <span slot="footer">
      <el-button type="primary" v-if="exist"  @click="updateCureForm"></el-button>
      <el-button type="primary" v-else  @click="createCureForm"></el-button>
      <el-button @click="dialogVisible=false"></el-button>
    </span>

  </el-dialog>
</template>
<script>
import { apiUpdatePatientDataForm, apiCreatePatientDataForm } from '@/api/api-prj002'
export default {
  name:'CureForm',
  data() {
    return {
      mydata: {
        'is_jiehe': '',
        xu:{'xu_yiqi': '','xu_bushen': '','xu_jian': '','xu_chong': '','xu_ziyin': '','xu_zhuyang': '','xu_buxue': '','xu_yangyin': ''},
        'xu_qita': '',
        shi:{'shi_xie': '','shi_tiao': '','shi_qing': '','shi_huo': '','shi_zao': '','shi_xue': '','shi_tan': ''},
        'shi_qita': '',
        xushi:{'xushi_qushi': '','xushi_yijing': '','xushi_hua': '','xushi_xue': '','xushi_ziyin': ''},
        'xushi_qita': '',
        'xuanji': '',
        'zucheng': '',
        'zhongcheng': '',
        'zhongcheng_juti': '-',
        'gaoxiong': '',
        'gaoxiong_juti': '-',
        'yidao': '',
        'yidao_juti': '-',
        'tiaozhou': '',
        'tiaozhou_juti': '-',
        'pailuan': '',
        'pailuan_juti': '-',
        'shoushu': '',
        'shoushu_juti': '-',
        'cure_qita': '',
      },
      cureForm:{
        is_jiehe: '',
        xu_yiqi: '',
        xu_bushen: '',
        xu_jian: '',
        xu_chong: '',
        xu_ziyin: '',
        xu_zhuyang: '',
        xu_buxue: '',
        xu_yangyin: '',
        xu_qita: '',
        shi_xie: '',
        shi_tiao: '',
        shi_qing: '',
        shi_huo: '',
        shi_zao: '',
        shi_xue: '',
        shi_tan: '',
        shi_qita: '',
        xushi_qushi: '',
        xushi_yijing: '',
        xushi_hua: '',
        xushi_xue: '',
        xushi_ziyin: '',
        xushi_qita: '',
        xuanji: '',
        zucheng: '',
        zhongcheng: '',
        zhongcheng_juti: '',
        gaoxiong: '',
        gaoxiong_juti: '',
        yidao: '',
        yidao_juti: '',
        tiaozhou: '',
        tiaozhou_juti: '',
        pailuan: '',
        pailuan_juti: '',
        shoushu: '',
        shoushu_juti: '',
        cure_qita: '',
        },
      dialogVisible: false,
      exist: true,
      formName:''
    }
  },
  methods: {
    updateCureForm () {
      apiUpdatePatientDataForm({formData:this.cureForm,formName:this.formName})
      .then((res)=> {
        this.resetDialog()
        this.$message({message: '',type: 'success'})
        this.dialogVisible = false
        this.$parent.getPatients()
      })
      .catch(
      )
    },
    createCureForm () {
      apiCreatePatientDataForm({formData:this.cureForm,formName:this.formName})
      .then((res)=> {
        this.resetDialog()
        this.$message({message: '',type: 'success'})
        this.dialogVisible = false
        this.$parent.getPatients()
      })
      .catch(
      )
    },
    resetDialog () {
      this.cureForm = {}
    }
  },
  created() {
    this.$on("openEvent", (data)=>{
      this.dialogVisible = true
      this.exist = data.exist
      this.formName = data.formName
      if (!data.exist) {
        //
        this.cureForm.info = data.formData.info
      } else {
        //()
        this.cureForm = data.formData
      }

    })
  }

};
</script>
<style lang="">
</style>


