<template>
  <el-dialog title="" class="my-dialog"
            :visible.sync="dialogVisible"
            :close-on-click-modal="false"
            width="90%" center>
    <el-form ref="infoForm" :model="infoForm" label-width="130px" label-position="right">
      <el-form-item label="">
        <el-input v-model="infoForm.name"></el-input>
        <!-- <el-input v-model="infoForm.name"><template slot="prepend"></template></el-input> -->
      </el-form-item>

      <el-form-item label="">
        <el-select v-model="infoForm.nation" placeholder="">
          <el-option v-for="item in nationSelection" :key="item" :label="item" :value="item">
          </el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="">
        <el-select v-model="infoForm.career" placeholder="">
          <el-option v-for="item in careerSelection" :key="item" :label="item" :value="item">
          </el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="">
        <el-checkbox v-for="(val, key) in specialCheckbox" :key="key" :label="val" v-model="infoForm[key]">
        </el-checkbox>
      </el-form-item>

      <el-form-item label="">
        <el-input v-model="infoForm.address"></el-input>
      </el-form-item>

      <el-form-item label="">
        <el-checkbox v-for="(val, key) in dietCheckbox" :key="key" :label="val" v-model="infoForm[key]">
        </el-checkbox>
        <el-input v-model="infoForm.yinshi_qita" placeholder=""></el-input>
      </el-form-item>

      <el-form-item label="">
        <el-input v-model="infoForm.height" type="number" min="0">
            <template slot="append">cm</template>
        </el-input>
      </el-form-item>

      <el-form-item label="">
        <el-input v-model="infoForm.weight" type="number" min="0">
          <template slot="append">kg</template>
        </el-input>
      </el-form-item>

      <el-form-item label="(BMI)">
        <el-tag>
          {{infoForm.weight2height = BMI }}kg/m<sup>2</sup>
        </el-tag>
      </el-form-item>

      <el-form-item label="">
        <el-input v-model="infoForm.hipline" type="number" min="0">
          <template slot="append">cm</template>
        </el-input>
      </el-form-item>

      <el-form-item label="">
        <el-input v-model="infoForm.waistline" type="number" min="0">
          <template slot="append">cm</template>
        </el-input>
      </el-form-item>

      <el-form-item label="(WHR)">
        <el-tag>
          {{infoForm.waist2hip =  waist2hip }}
        </el-tag>
      </el-form-item>

      <el-form-item label="">
        <el-input v-model="infoForm.hairy" type="number" min="0"></el-input>
      </el-form-item>

      <el-form-item label="">
        <el-switch v-model="infoForm.acne" active-text="" inactive-text=""></el-switch>
        <div v-show="infoForm.acne">
          <el-col :sm="24" :md="12">
            <el-input v-model="infoForm.acne_part">
              <template slot="prepend"></template>
            </el-input>
          </el-col>
          <el-col :sm="24" :md="12">
            <el-input v-model="infoForm.acne_score" type="number" min="0" max="5">
              <template slot="prepend"></template>
            </el-input>
          </el-col>
            <el-table :data="acneData" style="width: 100%">
              <el-table-column prop="field1" label=""></el-table-column>
              <el-table-column prop="field2" label=""></el-table-column>
              <el-table-column prop="field3" label=""></el-table-column>
            </el-table>
        </div>
      </el-form-item>

      <el-form-item label="">
        <el-switch v-model="infoForm.glandula" active-text="" inactive-text=""></el-switch>
        <div v-if="infoForm.glandula">
          <el-form-item label="">
            <el-input v-model="infoForm.glandula_part"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-radio-group v-model="infoForm.glandula_level">
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
            </el-radio-group>
          </el-form-item>
        </div>
      </el-form-item>

      <el-form-item label="">
        <el-switch v-model="infoForm.male" active-text="" inactive-text=""></el-switch>
        <div v-if="infoForm.male">
          <el-form-item label=",">
            <el-input v-model="infoForm.male_part"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-radio-group v-model="infoForm.male_level">
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
            </el-radio-group>
          </el-form-item>
        </div>
      </el-form-item>

    </el-form>
    <span slot="footer">
        <el-button type="primary" @click="updateInfoForm"></el-button>
        <el-button @click="dialogVisible=false"></el-button>
    </span>

  </el-dialog>
</template>
<script>
import { apiUpdatePatientDataForm } from '@/api/api-prj002'
export default {
  name: "InfoForm",
  data() {
    return {
      dialogVisible: false,
      infoForm: {},
      nationSelection: ["","","","","","","","","","","","",
                        "","","","","","","","","","","","",
                        "","","","","","","","","","","",
                        "","","","","","","","","","",
                        "","","","","","","","","","","",""],
      careerSelection: ["","","","","","","","","","","",""],
      specialCheckbox: {"special_gaowen":"","special_diwen":"","special_yeban":"","special_zao":"","special_fu":"","special_hua":"","special_ju":"","special_qi":"","special_kong":"","special_wu":""},
      dietCheckbox:    {"yinshi_wuteshu":"","yinshi_sushi":"","yinshi_suan":"","yinshi_xian":"","yinshi_xinla":"","yinshi_you":"","yinshi_shengleng":"","yinshi_cafei":""},
      acneData:[{field1: '0', field2: '', field3: ''},
                {field1: '1', field2: '', field3: '2mm<10'},
                {field1: '2', field2: '', field3: '10-20'},
                {field1: '3', field2: '', field3: '>20<20'},
                {field1: '4', field2: '', field3: '20'},
                {field1: '5', field2: '', field3: '5mm'}],
      exist: true,
      formName:''
    }
  },
  computed:{
    BMI () {
      const flag = (this.infoForm.weight==undefined) || (this.infoForm.height==undefined) || (this.infoForm.weight==null) || (this.infoForm.height==null) || Number(this.infoForm.height)== 0
      if (!flag) {
        var bmi = 10000 * Number(this.infoForm.weight) / (Number(this.infoForm.height) * Number(this.infoForm.height))
        return bmi.toFixed(1)
      }
      else {
        return 0
      }
    },
    waist2hip () {
      const flag = (this.infoForm.waistline==undefined) || (this.infoForm.hipline==undefined) || (this.infoForm.waistline==null) || (this.infoForm.hipline==null) || Number(this.infoForm.hipline)== 0
      if (!flag) {
        var ratio = Number(this.infoForm.waistline) / Number(this.infoForm.hipline)
        return ratio.toFixed(2)
      }
      else {
        return 0
      }
    }
  },
  methods: {
    showmsg(message){
      console.log(message)
    },
    updateInfoForm () {
      apiUpdatePatientDataForm({formData:this.infoForm,formName:this.formName})
      .then((res)=> {
        this.$message({message: '',type: 'success'})
        this.dialogVisible = false
        this.$parent.getPatients()
      })
      .catch(
        // this.$message({message: '',type: 'error'})
      )
    }
  },
  created() {
      this.$on("openEvent", (data)=>{
        console.log('',data)
        this.dialogVisible = true
        this.exist = data.exist
        this.formName = data.formName
        if (!data.exist) {
          //
          this.infoForm.info = data.formData.info
        } else {
          //()
          this.infoForm = data.formData
        }
      });
    }
};
</script>



