<template>
  <el-dialog title="" class="my-dialog"
            :visible.sync="dialogVisible"
            :close-on-click-modal="false" width="100%" center
            @close='resetDialog'>
    <el-form ref="clinicalForm" :model="clinicalForm" label-width="auto" label-position="left">

      <el-form-item label="">
          <el-checkbox v-for="(val, key) in mydata.zhongyi" :key="key" :label="val" v-model="clinicalForm[key]"></el-checkbox>
          <el-input v-model="clinicalForm.zhong_qita" placeholder=""></el-input>
      </el-form-item>

      <el-form-item label="">
        <el-form-item label="">
          <el-checkbox v-for="(val, key) in mydata.xuzheng" :key="key" :label="val" v-model="clinicalForm[key]"></el-checkbox>
          <el-input v-model="clinicalForm.xu_qita" placeholder="" ></el-input>
        </el-form-item>
        <!--  -->
        <!-- <el-form-item label="">
            <el-radio-group v-model="clinicalForm.xuzheng">
              <el-radio v-for="(val, key) in mydata.xuzheng" :key="key" :label="val">{{val}}</el-radio>
            </el-radio-group>
        </el-form-item> -->
        <el-form-item label="">
          <el-checkbox v-for="(val, key) in mydata.shizheng" :key="key" :label="val" v-model="clinicalForm[key]"></el-checkbox>
          <el-input v-model="clinicalForm.shi_qita" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-checkbox v-for="(val, key) in mydata.xushi" :key="key" :label="val" v-model="clinicalForm[key]"></el-checkbox>
          <el-input v-model="clinicalForm.xushi_qita" placeholder=""></el-input>
        </el-form-item>
      </el-form-item>

      <el-form-item label="">
        <el-checkbox label="" v-model="clinicalForm.xiyi_duonang"></el-checkbox>
        <el-input v-model="clinicalForm.xiyi_qita" placeholder="" ></el-input>
      </el-form-item>
    </el-form>

    <span slot="footer">
      <el-button type="primary" v-if="exist"  @click="updateClinicalForm"></el-button>
      <el-button type="primary" v-else  @click="createClinicalForm"></el-button>
      <el-button @click="dialogVisible=false"></el-button>
    </span>

  </el-dialog>
</template>
<script>
import { apiUpdatePatientDataForm, apiCreatePatientDataForm } from '@/api/api-prj002'
export default {
  name:'ClinicalForm',
  data() {
    return {
      mydata: {
        zhongyi:{"zhong_bijing":"", "zhong_buyun":"", "zhong_zhengjia":"", "zhong_benglou":"", "zhong_shitiao":""},
        xuzheng:{"xu_piqixu":"", "xu_pishenyangxu":"", "xu_piyangxu":"", "xu_shenqixu":"",
                "xu_shenyinxu":"", "xu_shenyangxu":"", "xu_xuexu":"", "xu_yinxuneire":""},
        shizheng:{"shi_shire":"", "shi_qizhi":"", "shi_yure":"", "shi_qizhixueyu":"",
                  "shi_tanshi":"", "shi_xueyu":"", "shi_hujie":""},
        xushi:{"xushi_pixutanshi":"", "xushi_shenxuganyu":"", "xushi_shenxutanshi":"", "xushi_shenxuxueyu":"", "xushi_shenxuneire":""}
      },
      clinicalForm: {
        "zhong_bijing":'', "zhong_buyun":'', "zhong_zhengjia":'', "zhong_benglou":'', "zhong_shitiao":'', "zhong_qita":'',
        "xu_piqixu":'', "xu_piyangxu":'', "xu_shenqixu":'', "xu_shenyinxu":'', "xu_shenyangxu":'', "xu_xuexu":'', "xu_yinxuneire":'', "xu_qita":'',
        "shi_shire":'', "shi_qizhi":'', "shi_yure":'', "shi_qizhixueyu":'', "shi_tanshi":'', "shi_xueyu":'', "shi_hujie":'', "shi_qita":'',
        "xushi_pixutanshi":'', "xushi_shenxuganyu":'', "xushi_shenxutanshi":'', "xushi_shenxuxueyu":'', "xushi_shenxuneire":'', "xushi_qita":'',
        "xiyi_duonang":'', "xiyi_qita":'',
      },
      dialogVisible: false,
      exist: true,
      formName:''
    }
  },
  methods: {
    updateClinicalForm () {
      apiUpdatePatientDataForm({formData:this.clinicalForm,formName:this.formName})
      .then((res)=> {
        this.resetDialog()
        this.$message({message: '',type: 'success'})
        this.dialogVisible = false
        this.$parent.getPatients()
      })
      .catch()
    },
    createClinicalForm () {
      apiCreatePatientDataForm({formData:this.clinicalForm,formName:this.formName})
      .then((res)=> {
        this.resetDialog()
        this.$message({message: '',type: 'success'})
        this.dialogVisible = false
        this.$parent.getPatients()
      })
      .catch()
    },
    resetDialog () {
      this.clinicalForm = {}
    }
  },
  created() {
    this.$on("openEvent", (data)=>{
      this.dialogVisible = true
      this.exist = data.exist
      this.formName = data.formName
      if (!data.exist) {
        //
        this.clinicalForm.info = data.formData.info
      } else {
        //()
        this.clinicalForm = data.formData
      }

    })
  }

};
</script>
<style lang="">
</style>


