<template>
  <el-dialog title="" class="my-dialog"
            :visible.sync="dialogVisible"
            :close-on-click-modal="false" width="100%" center
            @close='resetDialog'>
    <el-form ref="experimentForm" :model="experimentForm" label-width="210px" label-position="left">
      <el-form-item v-for="(val, key) in mydata" :key="key" :label="val">
        <!--  -->
        <el-input v-model="experimentForm[key]" type="number" min="0"></el-input>
      </el-form-item>

      <el-form-item label="(HOMA-IR)">
        <el-input type="number" min="0" v-model="HOMAIR" disabled></el-input>
      </el-form-item>
      <el-form-item label="(ISI)">
        <el-input type="number" min="0" v-model="ISI" disabled></el-input>
      </el-form-item>
    </el-form>

    <span slot="footer">
      <el-button type="primary" v-if="exist"  @click="updateExperimentForm"></el-button>
      <el-button type="primary" v-else  @click="createExperimentForm"></el-button>
      <el-button @click="dialogVisible=false"></el-button>
    </span>

  </el-dialog>
</template>
<script>
import { apiUpdatePatientDataForm, apiCreatePatientDataForm } from '@/api/api-prj002'
export default {
  name:'ExperimentForm',
  data() {
    return {
      experimentForm:{
          check_gaotong:'',           //T
          check_ci:'',                //E2
          check_huangti:'',           //LH
          check_luanpao:'',           //FSH
          check_xue:'',               //PRL
          check_xuetang:'',           //FPG
          check_kangmiao:'',          //AMH
          check_yidaosu:'',           //FINS
          check_canxue:'',            //30minsFPG
          check_canyi:'',             //30minsFINS
          check_cantang:'',           //60minsFPG
          check_candao:'',            //60minsFINS
          check_canxuetang:'',        //120minsFPG
          check_canyidao:'',          //1200minsFINS
          check_xuezhi:'',            //
          check_gaomizhi:'',          //
          check_dimizhi:'',           //
          yidaosu_dikang:'',          //HOMA-IR /22.5
          yidaosu_mingan:''           //ISI 1/
      },
      mydata:{
            'check_gaotong':'T','check_ci':'E2','check_huangti':'LH',
            'check_luanpao':'FSH','check_xue':'PRL','check_xuetang':'FPG',
            'check_yidaosu':'FINS','check_canxue':'30minsFPG',
            'check_canyi':'30minsFINS','check_cantang':'60minsFPG',
            'check_candao':'60minsFINS','check_canxuetang':'120minsFPG',
            'check_canyidao':'1200minsFINS','check_xuezhi':'','check_gaomizhi':'',
            'check_dimizhi':''
      },
      dialogVisible: false,
      exist: true,
      formName:''
    }
  },
  computed:{
    HOMAIR () {
      const ratio = Number(this.experimentForm.check_xuetang) * Number(this.experimentForm.check_yidaosu) / 22.5
      this.experimentForm.yidaosu_dikang = ratio.toFixed(2)
      return ratio.toFixed(2)
    },
    ISI () {
      const flag = (this.experimentForm.check_xuetang == undefined) || (this.experimentForm.check_yidaosu == undefined)
                || (this.experimentForm.check_xuetang == null) || (this.experimentForm.check_yidaosu == null)
                || Number(this.experimentForm.check_xuetang)== 0 || Number(this.experimentForm.check_yidaosu == 0)
      if (!flag) {
        const ratio = 1 / ( Number(this.experimentForm.check_xuetang) * Number(this.experimentForm.check_yidaosu) )
        this.experimentForm.yidaosu_mingan = ratio.toFixed(5)
        return ratio.toFixed(5)
      } else {
        this.experimentForm.yidaosu_mingan = 0
        return 0
      }
    }
  },
  methods: {
    updateExperimentForm () {
      apiUpdatePatientDataForm({formData:this.experimentForm,formName:this.formName})
      .then((res)=> {
        this.resetDialog()
        this.$message({message: '',type: 'success'})
        this.dialogVisible = false
        this.$parent.getPatients()
      })
      .catch()
    },
    createExperimentForm () {
      apiCreatePatientDataForm({formData:this.experimentForm,formName:this.formName})
      .then((res)=> {
        this.resetDialog()
        this.$message({message: '',type: 'success'})
        this.dialogVisible = false
        this.$parent.getPatients()
      })
      .catch()
    },
    resetDialog () {
      this.experimentForm = {}
    }
  },
  created() {
    this.$on("openEvent", (data)=>{
      this.dialogVisible = true
      this.exist = data.exist
      this.formName = data.formName
      if (!data.exist) {
        //
        this.experimentForm.info = data.formData.info
      } else {
        //()
        this.experimentForm = data.formData
      }
    })
  }

};
</script>
<style lang="">
</style>


