<template>
  <el-dialog title="" class="my-dialog"
            :visible.sync="dialogVisible"
            :close-on-click-modal="false" width="100%" center
            @close='resetDialog'>
    <el-form ref="historyForm" :model="historyForm" label-width="130px" label-position="right">

      <el-form-item label="">
        <el-radio v-model="historyForm.first_time"
                  v-for="item in mydata.first_time"
                  :key="item" :label="item">
        </el-radio>
        <el-input v-model="historyForm.first_time_qita" placeholder=""></el-input>
      </el-form-item>

      <el-form-item label="">
        <el-switch v-model="historyForm.is_normal" active-text="" inactive-text=""></el-switch>
      </el-form-item>
      <el-form-item v-show="historyForm.is_normal" label="">
        <el-radio v-model="historyForm.normal"
                  v-for="item in mydata.normal"
                  :key="item" :label="item">
        </el-radio>
      </el-form-item>
      <el-form-item v-show="!historyForm.is_normal" label="">
        <el-radio v-model="historyForm.abnormal"
                  v-for="item in mydata.abnormal"
                  :key="item" :label="item">
        </el-radio>
      </el-form-item>

      <el-form-item label="">
        <el-radio v-model="historyForm.cyclicity_sum"
                  v-for="item in mydata.cyclicity_sum"
                  :key="item" :label="item">
        </el-radio>
        <el-input v-model="historyForm.cyclicity_sum_qita" placeholder=""></el-input>
      </el-form-item>

      <el-form-item label="">
        <el-radio v-model="historyForm.blood_cond"
                  v-for="item in mydata.blood_cond"
                  :key="item" :label="item">
        </el-radio>
        <el-input v-model="historyForm.blood_cond_qita" placeholder=""></el-input>
      </el-form-item>

      <el-form-item label="">
        <el-radio v-model="historyForm.blood_color"
                  v-for="item in mydata.blood_color"
                  :key="item" :label="item">
        </el-radio>
        <el-input v-model="historyForm.blood_color_qita" placeholder=""></el-input>
      </el-form-item>

      <el-form-item label="">
        <el-radio v-model="historyForm.blood_quality"
                  v-for="item in mydata.blood_quality"
                  :key="item" :label="item">
        </el-radio>
        <el-radio v-model="historyForm.blood_block"
                  v-for="item in mydata.blood_block"
                  :key="item" :label="item">
        </el-radio>
      </el-form-item>

      <el-form-item label="">
        <el-radio v-model="historyForm.blood_character"
                  v-for="item in mydata.blood_character"
                  :key="item" :label="item">
        </el-radio>
      </el-form-item>

      <el-form-item label="">
        <el-switch v-model="historyForm.menstruation_is_accompany" active-text="" inactive-text=""></el-switch>
      </el-form-item>

      <el-form-item label="">
        <el-date-picker v-model="historyForm.last_time"
                        type="date" placeholder=""
                        format="yyyy  MM  dd "
                        value-format="yyyy-MM-dd">
        </el-date-picker>
      </el-form-item>

      <el-form-item label="">
        <el-select v-model="historyForm.leucorrhea_quantity" placeholder="">
          <template slot="prefix"></template>
          <el-option v-for="item in mydata.leucorrhea_quantity" :key="item" :value="item">
          </el-option>
        </el-select>
        <el-select v-model="historyForm.leucorrhea_color" placeholder="">
          <template slot="prefix"></template>
          <el-option v-for="item in mydata.leucorrhea_color" :key="item" :value="item">
          </el-option>
        </el-select>
        <el-select v-model="historyForm.leucorrhea_feature" placeholder="">
          <template slot="prefix"></template>
          <el-option v-for="item in mydata.leucorrhea_feature" :key="item" :value="item">
          </el-option>
        </el-select>
        <el-input v-model="historyForm.leucorrhea_color_qita" placeholder=""></el-input>
      </el-form-item>

      <el-form-item label="">
        <el-radio v-model="historyForm.marriage"
                  v-for="item in mydata.marriage"
                  :key="item" :label="item">
        </el-radio>
      </el-form-item>

      <el-form-item label="">
        <el-col :sm="24" :md="12" :lg="8" v-for="(val, key) in mydata.pastpreg" :key="key">
          <el-input type="number" min="0" v-model="historyForm[key]">
            <template slot="prepend">{{val}}</template>
            <template slot="append"></template>
          </el-input>
        </el-col>
          <el-input v-model="historyForm.pastpreg_qita" placeholder=""></el-input>
      </el-form-item>

      <el-form-item label="">
        <el-switch v-model="historyForm.prevent_wu" active-text="" inactive-text=""></el-switch>
        <div v-show="historyForm.prevent_wu">
          <el-checkbox v-model="historyForm.prevent_jieza" label="" ></el-checkbox>
          <el-checkbox v-model="historyForm.prevent_jieyuqi" label="" ></el-checkbox>
          <el-checkbox v-model="historyForm.prevent_biyuntao" label="" ></el-checkbox>
          <el-checkbox v-model="historyForm.prevent_biyunyao" label="" ></el-checkbox>
        </div>
      </el-form-item>

      <el-form-item label="">
        
        <el-switch v-model="historyForm.is_pastfamily_womb" active-text="" inactive-text=""></el-switch>
        <div v-show="historyForm.is_pastfamily_womb">
          <el-checkbox v-model="historyForm.pastfamily_minus"  label="" ></el-checkbox>
          <el-checkbox v-model="historyForm.pastfamily_plus"  label="" ></el-checkbox>
          <el-checkbox v-model="historyForm.pastfamily_duonangluanchao"  label="" ></el-checkbox>
          <el-checkbox v-model="historyForm.pastfamily_tangniaobing"  label="" ></el-checkbox>
          <el-checkbox v-model="historyForm.pastfamily_buxiang"  label="" ></el-checkbox>
          <el-input v-model="historyForm.pastfamily_qita"></el-input>
        </div>
      </el-form-item>

    </el-form>

    <span slot="footer">
      <el-button type="primary" v-if="exist"  @click="updateHistoryForm"></el-button>
      <el-button type="primary" v-else  @click="createHistoryForm"></el-button>
      <el-button @click="dialogVisible=false"></el-button>
    </span>
  </el-dialog>
</template>
<script>
import { apiUpdatePatientDataForm, apiCreatePatientDataForm } from '@/api/api-prj002'
export default {
  name:'HistoryForm',
  data() {
    return {
      mydata:{
        first_time:["10","11","14","16"],
        is_normal:true,
        normal:["21-25","26-30","31-35"],
        abnormal:["1","1-21","2-31","3-41","4-61",">61"],
        cyclicity_sum:["2","3-7","7"],
        blood_cond:["5()","6-10()","11-19()","20()",","],
        blood_color:["","","","","","","",],
        blood_quality:["","",""],
        blood_block:["","","","","",""],
        blood_character:["","","",""],
        leucorrhea_quantity:["","",""],
        leucorrhea_color:["","","",""],
        leucorrhea_feature:["","",""],
        marriage:["","","","","",""],
        pastpreg:{pastpreg_yuncount:'', pastpreg_shunchan:'', pastpreg_pougong:'',
                  pastpreg_zaochan:'', pastpreg_yaoliu:'', pastpreg_renliu:'',
                  pastpreg_ziranliu:'', pastpreg_yiweirenshen:'',
                  pastpreg_qinggongshu:''},
      },
      historyForm:{
        first_time:'', first_time_qita:'',            //
        is_normal:true, normal:'', abnormal:'',       //
        cyclicity_sum:'', cyclicity_sum_qita:'',      //
        blood_cond:'', blood_cond_qita:'',            //
        blood_color:'', blood_color_qita:'',          //
        blood_quality:'', blood_block:'',             //
        blood_character:'',                           //
        menstruation_is_accompany:'',                 //
        last_time:'',                                 //
        leucorrhea_quantity:'',                       //
        leucorrhea_color:'', leucorrhea_color_qita:'',//
        leucorrhea_feature:'',                        //
        marriage:'',                                  //
        //
        pastpreg_yuncount:'', pastpreg_shunchan:'', pastpreg_pougong:'', pastpreg_zaochan:'', pastpreg_yaoliu:'', pastpreg_renliu:'', pastpreg_ziranliu:'', pastpreg_yiweirenshen:'', pastpreg_qinggongshu:'', pastpreg_qita:'',
        //
        prevent_wu:'', prevent_jieza:'', prevent_jieyuqi:'', prevent_biyuntao:'', prevent_biyunyao:'',
        //
        is_pastfamily_womb:'', pastfamily_minus:'', pastfamily_plus:'', pastfamily_duonangluanchao:'', pastfamily_tangniaobing:'', pastfamily_buxiang:'', pastfamily_qita:''
      },
      dialogVisible: false,
      exist: true,
      formName:''
    }
  },
  methods: {
    updateHistoryForm () {
      apiUpdatePatientDataForm({formData:this.historyForm, formName:this.formName})
      .then((res)=> {
        this.resetDialog()
        this.$message({message: '',type: 'success'})
        this.dialogVisible = false
        this.$parent.getPatients()
      })
      .catch(
      )
    },
    createHistoryForm () {
      apiCreatePatientDataForm({formData:this.historyForm,formName:this.formName})
      .then((res)=> {
        this.resetDialog()
        this.$message({message: '',type: 'success'})
        this.dialogVisible = false
        this.$parent.getPatients()
      })
      .catch(
      )
    },
    resetDialog () {
      this.historyForm = {}
    }
  },
  created() {
    this.$on("openEvent", (data)=>{
      this.dialogVisible = true
      this.exist = data.exist
      this.formName = data.formName
      if (!data.exist) {
        //
        this.historyForm.info = data.formData.info
      } else {
        //()
        this.historyForm = data.formData
      }
    })
  }

};
</script>
<style scoped>
.el-input {
  margin-top: 10px;
}
</style>


