<template>
  <el-dialog title="" class="my-dialog"
            :visible.sync="dialogVisible"
            :close-on-click-modal="false" width="70%" center>

    <el-form ref="patientInfo" :model="patientInfo" :rules="rules" label-width="130px" label-position="left">

      <el-form-item label="" prop="name">
        <el-input v-model="patientInfo.name"></el-input>
      </el-form-item>

      <el-form-item label="" prop="phone">
        <el-input v-model="patientInfo.phone"></el-input>
      </el-form-item>

      <el-form-item label="" prop="hospital">
        <el-input v-model="patientInfo.hospital"></el-input>
      </el-form-item>

      <el-form-item label="" prop="birth">
        <!-- format, value-format -->
        <el-date-picker v-model="patientInfo.birth"
                        type="month" placeholder=""
                        format="yyyy  MM "
                        value-format="yyyy-MM">
        </el-date-picker>
      </el-form-item>

      <el-form-item label="" prop="career">
        <el-select v-model="patientInfo.career" placeholder="">
          <el-option v-for="item in careerSelection" :key="item" :label="item" :value="item">
          </el-option>
        </el-select>
      </el-form-item>

    </el-form>
      <span slot="footer">
        <el-button type="primary" @click="addPatient"></el-button>
        <el-button @click="dialogVisible=false"></el-button>
      </span>
  </el-dialog>
</template>

<script>
import { apiAddPatient } from '@/api/api-prj002'
export default {
    name:'AddPatient',
    data () {
      return {
        patientInfo: {name:'', phone:'13110983476', hospital:'', birth:'1980-09', career:''},
        // patientInfo: {name:'', phone:'', hospital:'', birth:'', career:''},
        careerSelection: ["","","","","","","","","","","",""],
        dialogVisible: false,
        rules:{
          name: [
            {required: true, message: '', trigger: 'blur' }
          ],
          phone: [
            {required: true, pattern: /^1\d{10}$/, message: '11',trigger: 'blur'}
          ],
          hospital:[{required: true, message: ''}],
          birth:   [{required: true, message: ''}],
          career:  [{required: true, message: ''}]
        }
      }

    },
    methods: {
      addPatient () {
        this.$refs.patientInfo.validate( (valid) => {
          if (valid) {
            let para = {
              patientInfo: this.patientInfo
            }
            apiAddPatient(para)
            .then( (res)=> {
              this.$message({message: '',type: 'success'})
              this.dialogVisible = false
              this.$parent.getPatients()
              }
            )
            .catch()
          } else {
            this.$message({message: '',type: 'warning'})
            return false;
          }
        })

      }
    },
    created() {
      this.$on("addEvent", function(data) {
        this.dialogVisible = true;
      });
    }
}

    // // 
    // addSubmit: function () {
    //   this.$refs.addForm.validate((valid) => {
    //     if (valid) {
    //       this.$confirm('', '', {}).then(() => {
    //         this.addLoading = true
    //         // NProgress.start();
    //         let para = Object.assign({}, this.addForm)
    //         para.birth = (!para.birth || para.birth === '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd')
    //         addUser(para).then((res) => {
    //           this.addLoading = false
    //           // NProgress.done();
    //           this.$message({
    //             message: '',
    //             type: 'success'
    //           })
    //           this.$refs['addForm'].resetFields()
    //           this.addFormVisible = false
    //           this.getPatients()
    //         })
    //       })
    //     }
    //   })
    // },
</script>

<style lang="scss">

</style>



