<template>
  <section>
    <!-- -->
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
      <el-form :inline="true" :model="search">
        <el-form-item v-for="(val, key, index) in search" :key="index">
          <el-input v-model="search[key]" :placeholder="searchName[key]"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" icon="el-icon-search" @click="searchPatient"></el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" icon="el-icon-refresh-right" @click="getPatients"></el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="addPatient"></el-button>
        </el-form-item>
      </el-form>
    </el-col>

    <!-- -->
    <el-table :data="patientsList" highlight-current-row v-loading="listLoading" @selection-change="selsChange"
              style="width: 100%;" height="500"><!--height-->
      <!-- <el-table-column type="selection" width="55">
      </el-table-column> -->
      <el-table-column type="index" width="40">
      </el-table-column>
      <el-table-column prop="name" label="" width="90" sortable>
      </el-table-column>
      <el-table-column prop="birth" label="" width="100">
      </el-table-column>
      <el-table-column prop="phone" label="" width="110">
      </el-table-column>
      <el-table-column prop="hospital" label="" width="150">
      </el-table-column>
      <el-table-column prop="career" label="" min-width="90">
      </el-table-column>
      <el-table-column label="" width="610">
        <template v-slot="scope">
          <el-button-group>
          <el-button type="btn-info" size="small" @click="openDataForm(scope.$index, scope.row, 'info')"></el-button>
          <el-button type="btn-summary" size="small" @click="openDataForm(scope.$index, scope.row, 'summary')"></el-button>
          <el-button type="btn-history"    size="small" @click="openDataForm(scope.$index, scope.row, 'history')"></el-button>
          <el-button type="btn-experiment" size="small" @click="openDataForm(scope.$index, scope.row, 'experiment')"></el-button>
          <el-button type="btn-bxray" size="small" @click="openDataForm(scope.$index, scope.row, 'bxray')">B</el-button>
          <el-button type="btn-clinical" size="small" @click="openDataForm(scope.$index, scope.row, 'clinical')"></el-button>
          <el-button type="btn-cure" size="small" @click="openDataForm(scope.$index, scope.row, 'cure')"></el-button>
          </el-button-group>
          <el-button type="danger" size="small" style="margin-left:8px" @click="handleDel(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!---->
    <el-col :span="24" class="toolbar">
      <!-- <el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button> -->
      <!-- v-on,,$event, -->
      <!-- v-onFunction | Inline Statement,() FunctionInline Statement-->
      <el-pagination layout="total, prev, pager, next, jumper"
                    @current-change="pagination_flag ? handleListPagination($event,page) : handleSearchPagination($event,search_page)"
                    :page-size="10" :total="totalNum"
                    style="float:right;">
      </el-pagination>
    </el-col>

    <!-- dialog -->
    <InfoForm ref="info"></InfoForm>
    <!-- dialog -->
    <SummaryForm ref="summary"></SummaryForm>
    <!-- dialog -->
    <HistoryForm ref="history"></HistoryForm>
    <!-- dialog -->
    <ExperimentForm ref="experiment"></ExperimentForm>
    <!-- Bdialog -->
    <BxrayForm ref="bxray"></BxrayForm>
    <!-- dialog -->
    <ClinicalForm ref="clinical"></ClinicalForm>
    <!-- dialog -->
    <CureForm ref="cure"></CureForm>

    <!-- dialog -->
    <AddPatient ref="addPatient" ></AddPatient>

  </section>
</template>

<script>
import util from '@/common/js/util'
// axios,express
import {apiGetPatientsList, apiRemovePatient, apiSearchPatient, batchRemoveUser} from '@/api/api-prj002'
// api
import {apiGetPatientDataForm } from '@/api/api-prj002'
// 
import {AddPatient,InfoForm,SummaryForm,HistoryForm,ExperimentForm,BxrayForm,ClinicalForm,CureForm} from '@/components/prj002/forms'
export default {
  name:'Table',
  components:{AddPatient,InfoForm,SummaryForm,HistoryForm,ExperimentForm,BxrayForm,ClinicalForm,CureForm},
  data () {
    return {
      search: {
        name: '', phone:'', hospital:'', birth:'', career:'', address:''
      },
      searchName: {name:'',phone:'',hospital:'',birth:'',career:'',address:''},
      patientsList: [], // 
      totalNum: 0, //  
      page: 1, //
      search_page: 1, //
      listLoading: false,
      sels: [], // 
      pagination_flag: true //true,false
    }
  },
  methods: {
    // dialog
    addPatient () {
      this.$refs.addPatient.$emit("addEvent")
    },
    // 
    searchPatient () {
      console.log('',this.search)
      let para = {
        page: this.search_page,
        search:this.search
      }
      this.pagination_flag = false
      apiSearchPatient(para).then( (res) => {
        // console.log('',res.data.searchResults)
        this.patientsList = res.data.searchResults
        this.totalNum = res.data.searchResultsNum
        this.listLoading = false
      })
    },
    handleListPagination (currentPage) {
      console.log('',currentPage)
      this.page = currentPage,
      this.getPatients()
    },
    handleSearchPagination (currentPage) {
      console.log('',currentPage)
      this.search_page = currentPage
      this.searchPatient()
    },
    // 
    getPatients () {
      this.search = {name: '', phone:'', hospital:'', birth:'', career:'', address:''}
      let para = {
        page: this.page
      }
      this.listLoading = true
      apiGetPatientsList(para).then((res) => {
        console.log(res.data)
        this.patientsList = res.data.patientsList
        this.totalNum = res.data.totalNum
        this.listLoading = false
      })
    },
    // 
    handleDel: function (index, row) {
      this.$confirm('?', '', {type: 'warning'})
      .then(() => {
        this.listLoading = true
        let para = { url: row.url }
        console.log(row.url)
        apiRemovePatient(para).then((res) => {
          this.listLoading = false
          this.$message({
            message: '',
            type: 'success'
          })
          this.getPatients()
        })
      }).catch(() => {})
    },

    selsChange: function (sels) {
      this.sels = sels
    },
    // 
    batchRemove: function () {
      var ids = this.sels.map(item => item.id).toString()
      this.$confirm('', '', {
        type: 'warning'
      }).then(() => {
        this.listLoading = true
        let para = { ids: ids }
        batchRemoveUser(para).then((res) => {
          this.listLoading = false
          this.$message({
            message: '',
            type: 'success'
          })
          this.getPatients()
        })
      }).catch(() => {

      })
    },

    //Form
    openDataForm (index, row, formName) {
      console.log('formName',formName)

      // if (formName == 'info') {
      //   console.log("",row);
      //   row[formName] = row['url'];
      //   console.log("",row)}

      // DataForm,,
      if (row[formName]==null) {
        console.log('')
        // DataFormurl,urlinfourl
        this.$refs[formName].$emit("openEvent", {exist:false, formData:{info:row.url}, formName:formName } )
      // DataForm,,
      } else {
        console.log('')
        // DataForm
        console.log('row[formName]',row[formName])
        let para = {page: this.page, url: row[formName]}
        apiGetPatientDataForm(para)
        .then((res)=> {
          console.log('DataForm',res.data)
          this.$refs[formName].$emit("openEvent", {exist:true, formData:res.data})
        })
        .catch(() => {})
      }
    },

  },
  mounted () {
    this.getPatients()
  }
}

</script>

<style scoped>
.el-button--btn-info {
  background:#a8d8ea;
  border:1px solid #a8d8ea
}
.el-button--btn-summary {
  background:#aa96da;
  border:1px solid #aa96da
}
.el-button--btn-history {
  background:#fcbad3;
  border:1px solid #fcbad3
}
.el-button--btn-experiment {
  background:#ffffd2;
  border:1px solid #ffffd2
}
.el-button--btn-bxray {
  background:#c7f0db;
  border:1px solid #c7f0db
}
.el-button--btn-clinical {
  background:#41b6e6;
  border:1px solid #41b6e6
}
.el-button--btn-cure {
  background:#d7d1c9;
  border:1px solid #d7d1c9
}
.el-button--myinfo {
background:red;
border:1px solid red;
}
</style>



